










import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

interface User {
  name: string;
  img: string;
  birthday: string;

}

@Component
export default class UserMsg extends Vue {
  @Prop({
    type: Object,
    required: true,
  }) data: User;
  user: User;
  showUser: boolean = true;
  constructor(props) {
    super(props);
    this.user = this.data;
  }
}



